import { useSession, signOut } from "next-auth/react";  // Use session for role-based access

// Extend the Session type to include the role property
declare module "next-auth" {
  interface Session {
    user: {
      role?: string;
    };
  }
}
import Link from "next/link";
import { 
  Home, Users, Tag, ClipboardList, Coins, ShoppingCart, BarChart, CalendarCheck, CreditCard, 
  MessageSquare, Bell, UserCircle, Settings, LogOut, Boxes, Package 
} from "lucide-react"; // Importing corrected icons

const menuItems = [
  {
    title: "MENU",
    items: [
      { icon: Home, label: "Home", href: "/", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: Users, label: "Store Managers", href: "/DashView/list/StoreManager", visible: ["admin", "storeManager"] },
      { icon: Users, label: "Sales Associates", href: "/list/students", visible: ["admin", "storeManager"] },
      { icon: Users, label: "Cashiers", href: "/list/parents", visible: ["admin", "storeManager"] },
      { icon: ClipboardList, label: "Suppliers", href: "/DashView/list/Supplier", visible: ["admin"] },
      { icon: ClipboardList, label: "Orders", href: "/list/classes", visible: ["admin", "storeManager"] },
      { icon: Boxes, label: "Jewellery Stock", href: "/list/lessons", visible: ["admin", "storeManager"] },
      { icon: Coins, label: "Gold Stock", href: "/list/exams", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: Tag, label: "Categories", href: "/list/assignments", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: ShoppingCart, label: "Sales", href: "/list/results", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: BarChart, label: "Reports", href: "/list/attendance", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: CalendarCheck, label: "Events", href: "/list/events", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: CreditCard, label: "Advance Payment", href: "/list/messages", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: Bell, label: "Notifications", href: "/list/announcements", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
    ],
  },
  {
    title: "OTHER",
    items: [
      { icon: UserCircle, label: "Profile", href: "/profile", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: Settings, label: "Settings", href: "/settings", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
      { icon: LogOut, label: "Logout", href: "/logout", visible: ["admin", "storeManager", "Sales Associate", "parent"] },
    ],
  },
];

const Menu = () => {
  // Get user session and role
  const { data: session } = useSession();
  const role = session?.user?.role || "guest";  // Default to 'guest' if role is not found
  
  return (
    <div className="mt-4 text-sm bg-[#FFF6BD] p-4 rounded-md w-64">
      {menuItems.map((section) => (
        <div className="flex flex-col gap-2" key={section.title}>
          <span className="hidden lg:block text-black font-semibold my-4">
            {section.title}
          </span>
          {section.items.map((item) => {
            if (item.visible.includes(role)) {
              return (
                <Link
                  href={item.href}
                  key={item.label}
                  className="flex items-center justify-start gap-4 text-black py-2 px-4 rounded-md hover:bg-[#F0A500] hover:text-white transition duration-200"
                >
                  <item.icon size={20} className="text-black" />
                  <span className="hidden lg:block">{item.label}</span>
                </Link>
              );
            }
            return null; // Don't render items that the user shouldn't see
          })}
        </div>
      ))}
    </div>
  );
};

export default Menu;
